<template>
  <div
    v-for="(item, idx) in aboutItems"
    :key="idx"
    class="card-box__item"
    @click="selectAboutItem(idx)"
  >
    <div
      :class="[
        'card-box__item__title',
        { 'card-box__item__title--active': selectedAboutItem === idx },
      ]"
    >
      {{ item.title }}
    </div>
    <Transition name="expand" @enter="enter" @leave="leave">
      <div v-if="selectedAboutItem === idx" class="card-box__item__desc">
        {{ item.desc }}
      </div>
    </Transition>
  </div>
</template>

<script>
export default {
  name: "Cardbox",
  data() {
    return {
      aboutItems: [
        {
          title: "Digestive Health",
          desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed doeiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim adminim veniam, quis nostrud exercitation ullamco laboris nisi utaliquip ex ea commodo consequat",
        },
        {
          title: "Digestive Health",
          desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed doeiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim adminim veniam, quis nostrud exercitation ullamco laboris nisi utaliquip ex ea commodo consequat",
        },
        {
          title: "Digestive Health",
          desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed doeiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim adminim veniam, quis nostrud exercitation ullamco laboris nisi utaliquip ex ea commodo consequat",
